<template>
  <div class="index">
    <div class="phone-number-wrapper">
      <input class="phone-number" type="number" oninput=" if(value.length>11)
      {value=value.slice(0,11)}" v-model="number1">
      <input class="phone-number" type="text" oninput=" if(value.length>11)
      {value=value.slice(0,11)}" v-model="number2">
    </div>

    <div class="recharge-wrapper">
      <div class="recharge-title">请选择UG币充值数量</div>
      <div class="recharge-amount-wrapper">
        <div class="amount" v-for="(item,index) in amount" @click="nowIndex = index"
             :class="{active:index === nowIndex}">
          <p class="ugb">{{item.ug}}UG币</p>
          <p class="prize">￥{{item.amount}}</p>
        </div>
      </div>
    </div>
    <router-link :to="{name:'Rule'}">
      <div class="pic-wrapper"></div>
    </router-link>
    <div class="button-wrapper" @click="recharge">
      立即支付{{rechargeAmount}}元
    </div>
  </div>
</template>

<script>
  import {ajaxRequest} from '../../../../common/js/common';
  import config from '../../../../common/js/config';
  import '../../../../common/js/share';
  // import {Toast} from 'mint-ui';

  export default {
    name: 'App',
    data() {
      // var url = 'http%3a%2f%2fapp.ugaming.com.cn%2fwx%2ftransform.html%3f';
      // var type = 'type%3d' + this.typeId;
      // var phone = '%26phone%3d' + this.number1;
      // var name = '%26name%3d' + this.amount[this.nowIndex].name;
      return {
        code: '',
        number1: '',
        number2: '',
        nowIndex: 0,
        amount: [
          {
            id: '',
            kind: '',
            amount: '',
            ug: '',
            name: ''
          }
        ]
      };
    },
    computed: {
      rechargeAmount() {
        return this.amount[this.nowIndex].amount;
      },
      url() {
        return 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxeeae5c0ace4e4501&redirect_uri=' +
          'http%3a%2f%2fapp.ugaming.com.cn%2fwx%2ftransform.html%3f' + 'type%3d' + this.typeId + '%26phone%3d' + this.number1 + '%26name%3d' + this.amount[this.nowIndex].name + '&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect';
      },
      typeId() {
        var IP = document.location.hostname;
        if (IP === 'app.ugaming.com.cn') { // 正式
          return 0;
        } else if (IP === 'app-test.ugaming.com.cn') { // 测试
          return 1;
        } else {
          return 2;
        }
      }
    },
    methods: {
      recharge() {
        window.location = this.url;
        // ajaxRequest({
        // });
        //
        // }
        // if (this.number1 == '') {
        //   Toast({
        //     message: '请输入手机号！'
        //   });
        // } else if (this.number1 != this.number2) {
        //   Toast({
        //     message: '两次手机号不一致！'
        //   });
        // } else if (this.number1 == this.number2 && this.number1.length != 11) {
        //   Toast({
        //     message: '手机号不存在！'
        //   });
        // } else {
        //   Toast({
        //     message: '充值成功请到APP查看余额变化！'
        //   });
        // }
      }
    },
    created() {
      var self = this;
      if (this.$route.query.code) {
        var code = this.$route.query.code;
        var phone = this.$route.query.phone;
        var name = this.$route.query.name;
        ajaxRequest({
          url: config.GET_MPCHARGE_URL,
          type: config.GET_MPCHARGE_TYPE,
          data: JSON.stringify({
            chargeInfoName: name,
            code: code,
            phone: phone
          }),
          successFun(result) {
            wx.chooseWXPay({
              timestamp: result.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
              package: result.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
              signType: result.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              paySign: result.sign, // 支付签名
              success: function (res) {
                // 支付成功后的回调函数
                wx.chooseWXPay({
                  timestamp: res.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                  nonceStr: res.nonceStr, // 支付签名随机串，不长于 32 位
                  package: 'prepay_id=res.package', // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                  signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                  paySign: res.sign, // 支付签名
                  success: function (res) {
// 支付成功后的回调函数
                  }
                });
              }
            });
          }
        });
      };
        ajaxRequest({
          url: config.GET_CHARGEINFO_URL('mp'),
          type: config.GET_CHARGEINFO_TYPE,
          successFun(result) {
            self.amount = result.list;
          }
        });
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../../../common/stylus/mixin.styl';
  .index
    width: 100%
    font-size: 24px
    color: #1d1d1d
    .phone-number-wrapper
      margin-top: 49px
      text-align: center
      .phone-number
        width: 86%
        height: 100px
        margin-bottom: 22px
        padding-left: 35px
        font-size: 28px
        line-height: 100px
        border-radius: 10px
        border: 1px solid #9e9e9e
    .recharge-wrapper
      .recharge-title
        margin-top: 40px
        margin-left: 40px
      .recharge-amount-wrapper
        margin-top: 47px
        .amount
          display: inline-block
          width: 190px
          height: 85px
          margin-left: 44px
          margin-bottom: 36px
          border-radius 30px
          border: 1Px solid black
          text-align: center
          line-height: 30px
          .prize
            color: #9e9e9e
          .ugb
            margin-top: 15px
        .amount.active
          display: inline-block
          width: 190px
          height: 85px
          margin-left: 44px
          margin-bottom: 36px
          border-radius 30px
          border: 1Px solid black
          text-align: center
          line-height: 30 spx
          background-color: rgb(63, 59, 56)
          color: white
    .pic-wrapper
      display: block
      width: 90%
      height: 165px
      margin-left: 5%
      margin-top: 14px
      bg-image('../../../../assets/firstRecharge_01')
      background-size: 100% 165px
    .button-wrapper
      width: 90%
      height: 100px
      margin-top: 60px
      margin-left: 5%
      line-height: 100px
      text-align: center
      color: white
      bg-image('../../../../assets/alertBtn')
      background-size: 100% 165px
</style>



